<template>
  <div>
    <el-dialog :close-on-click-modal="false" title="选择三包换胎出库信息" :visible="true" width="1300px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="商品名称" size="mini" v-if="TableData.tableData.length>0">
            <span style="width: 200px;display: inline-block;">{{TableData.tableData[0].goods_name}}</span>
          </el-form-item>
          <el-form-item label="商品编号" size="mini" v-if="TableData.tableData.length>0">
            <span>{{TableData.tableData[0].goods_id}}</span>
          </el-form-item>
          <br>
          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" placeholder="请选择" @focus="getWarehouse" @change="changeAll">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList.options"></el-option>
            </el-select>
            <!-- <el-input v-model="params.goods_name" class="shortSearchInput"></el-input> -->
          </el-form-item>
          <el-form-item label="货位名称" size="mini">
            <el-select clearable class="el_slect" v-model="params.allocation_id" placeholder="请选择" size="mini">
              <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
            </el-select>
            <!-- <el-input v-model="params.company_claim_sn" class="shortSearchInput"></el-input> -->
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @row-click="handleCurrentChanges" v-loading="loading1" max-height="600">
          <el-table-column :show-overflow-tooltip="true" property="batch_id" class="radio_scope" label="选择" width="50">
            <template slot-scope="scope">
              <el-radio v-model="radio" :label="scope.row.batch_id">{{null}}</el-radio>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="available_stocks" label="可用库存">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()">取 消</el-button>
        <el-button type="primary" @click="dialogSubmitForm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      params: {},
      TableData: {
        tableData: [],
        total: 10
      },
      warehouseList: {
        options: []
      }, //仓库管理
      allocationInventoryList: [], //货位列表
      radio: "",
      loading: true,
      searchParams: {},
      goPage: 1, //默认搜索页面
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      multipleSelection: null //已选项
    };
  },
  props: ["stockInfo"],
  methods: {
    getWarehouse() {
      this.$common.options("getWarehouseListOption", this.warehouseList, {
        company_id: localStorage.getItem("company_id")
      }); //仓库管理列表
    },
    dialogSubmitForm() {
      if (this.multipleSelection != null) {
        let index = this.stockInfo.index;
        let obj = Object.assign(
          {},
          this.$parent.TableData.tableData[index],
          this.multipleSelection
        );
        this.$set(this.$parent.TableData.tableData, index, obj);
        this.$parent.stock_data = false;
      }
    },
    closeDialog() {
      this.$parent.stock_data = false;
    },
    handleCurrentChanges(val) {
      this.multipleSelection = val;
    },
    onSearch() {
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    onReset() {
      this.$set(this, "params", {});
      this.$set(this, "searchParams", {
        params: {
          company_id: this.stockInfo.company_id,
          goods_id: this.stockInfo.goods_id
        }, //1默认带的参数
        TableData: this.TableData, //列表数据
        API: "getGoodsBatchList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams); //获取销售订单列表
    },
    changeAll(val) {
      this.$set(this.params, "allocation_id", "");
      this.$axMethod
        .allocationInventoryList({
          warehouse_id: val
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.allocationInventoryList = data.data;
          } //货位列表下拉
        });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  created() {
    this.$set(this, "searchParams", {
      params: {
        company_id: this.stockInfo.company_id,
        goods_id: this.stockInfo.goods_id
      }, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getGoodsBatchList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
  }
};
</script>

<style scoped>
.el-radio__label {
  padding: 0px;
}
</style>
