 *注意
 * status:1 查看客户返利 status:2 选择客户返利  
 * 需传入 客户列表、返利类型列表和调取客户返利列表的参数
 */ 
 
<template>
  <div class="search_container">
    <!--客户返利 弹框-->
    <!-- class="dialogForm" -->
    <el-dialog :close-on-click-modal="false" :title="info.title" :visible="true" width="1100px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeVal" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="选择客户" size="mini">
            <el-select clearable v-model="formInline.customer_id" filterable placeholder="请选择">
              <el-option v-for="item in selesTomerList.options" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <span style="float: left">{{ item.customer_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="返利类型" size="mini">
            <el-select clearable v-model="formInline.rebate_type_id" placeholder="请选择">
              <el-option v-for="item in rebateTypeList.options" :key="item.rebate_id" :label="item.rebate_name" :value="item.rebate_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="返利单据号" size="mini">
            <el-input v-model="formInline.consignee_name" clearable placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch"  size="mini">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" max-height="600">
          <el-table-column :show-overflow-tooltip="true" type="selection"  fixed="left"  align="center" width="44" label="选择" v-if="!info.flag">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="rebate_sn" label="返利单据号" width="180"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" align="right" property="total_price" label="返利金额">
            <template slot-scope="scope">
              {{scope.row.total_price|priceFormat}}
            </template>
          </el-table-column>
          <!-- <el-table-column  :show-overflow-tooltip="true"  property="is_use" :formatter="formatStatus" label="是否使用">
          </el-table-column> -->
          <el-table-column :show-overflow-tooltip="true" property="rebate_name" label="返利类型">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="cycle_name" label="返利周期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>

      <div slot="footer" class="dialog-footer" v-if="!info.flag">
        <el-button @click="closeDialog()">取 消</el-button>
        <el-button type="primary" @click="submit()">确 定</el-button>
      </div>
      <div slot="footer" class="dialog-footer" v-else>
        <el-button @click="closeDialog()">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      TableData: {
        tableData: [],
        total: 10
      },
      per_page: [10, 20, 30],
      manageList: [], //仓库
      multipleSelection: [],
      formInline: {},
      params: {
        customer_id: String(this.selectData.customer_id),
        is_use: this.selectData.is_use
      },
      timeVal: null,
      info: {}
    };
  },
  props: {
    //客户列表
    selesTomerList: {
      type: [Object],
      default: []
    },
    //返利类型列表
    rebateTypeList: {
      type: [Object],
      default: []
    },
    selectData: {
      type: [Object]
    },
    status: {
      default: "1" //弹框状态 1：查看 2 选择
    },
    auditorStatus: {
      default: 0
    }
  },
  methods: {
    closeDialog() {
      this.$parent.showRebateCustomVisible = false;
    },
    formatStatus(row) {
      return row.is_use == 0 ? "否" : "是";
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      let page = {
        page: val,
        ...this.params,
        ...this.formInline
      };
      this.$common.handleCurrentChange(
        "getCustomerRebateList",
        page,
        this.TableData
      );
    },

    onSearch() {
      if (this.timeVal) {
        this.params.start_time = this.timeVal[0];
        this.params.end_time = this.timeVal[1];
      }
      Object.assign(this.params, this.formInline);
      //查询
      this.$common.initData(
        "getCustomerRebateList",
        this.TableData,
        this.params
      );
    },
    submit() {
      // if (this.multipleSelection.length) {
      this.$emit("getRebateCustom", this.multipleSelection);
      this.closeDialog();
      // }
      // else {
      //   this.$message({
      //     message: "请选择客户返利",
      //     type: "warning"
      //   });
      // }
    }
  },
  mounted() {
    if (this.status == 1) {
      this.info = {
        flag: true, //仅查看
        title: "查看客户返利"
      };
    } else {
      this.info = {
        flag: false, //可操作
        title: "选择客户返利"
      };
    }
    if (this.auditorStatus == 2) {
      this.params.auditor_status = 2;
    }
    this.$common.initData("getCustomerRebateList", this.TableData, this.params);
  }
};
</script>