<template>
  <div v-clock>
    <!--引用销售出库单据-->
    <el-dialog :close-on-click-modal="false" title="引用公司理赔数据" :visible="true" class="dialogForm" width="1300px" @close="closeDialog">

      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="审核状态" size="mini">
            <span>已审核</span>
          </el-form-item>
          <br>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item label="客户名称" :rules="rules.customer_id" prop="customer_id" size="mini">
            <el-select v-model="params.customer_id" clearable filterable placeholder="请输入" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" class="shortSearchInput" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="公司理赔单据号" size="mini">
            <el-input v-model="params.company_claim_sn" class="shortSearchInput" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" class="shortSearchInput" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1" max-height="450">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_claim_sn" width="150px" label="公司理赔单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="150">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号" min-width="130">
          </el-table-column>
          <!-- <el-table-column :show-overflow-tooltip="true" property="goods_price" label="商品售价" align="right" min-width="130">
          </el-table-column> -->
          <el-table-column :show-overflow-tooltip="true" property="goods_price" label="原销售价" align="right" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="discount_price" label="原折后价" align="right" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="new_goods_price" label="最新销售价" align="right" min-width="130">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="abrasion_price" label="磨损费" align="right" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="intube_price" label="内垫费" align="right" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="claim_price" label="理赔单价" align="right" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" min-width="130">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="[30, 50, 100,500]" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog()">取 消</el-button>
        <el-button class="tx" type="primary" @click="dialogSubmitForm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/sever/api";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      authority: {},
      TableData: {
        tableData: [],
        total: 10
      },
      goPage: 1, //默认搜索页面
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      params: {},
      loading: true,
      searchParams: {},
      timeValue: [],
      dialogmultipleSelection: [], //客户列表
      addmultipleSelection: []
    };
  },
  props: {
    companyId: {
      type: [Number, String]
    },
    customerId: {
      type: [Number, String]
    },
    customerName: {
      type: [Number, String]
    }
  },
  methods: {
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    handleSelectionChange(val) {
      this.addmultipleSelection = val;
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {
        company_id: this.companyId,
        customer_id: this.customerId,
        customer_name: this.customerName
      });
      this.timeValue = [];

      this.$set(this, "searchParams", {
        params: {
          customer_id: this.customerId
        },
        TableData: this.TableData, //列表数据
        API: "referenceClaimList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    closeDialog() {
      this.$parent.salesDocumentsVisible = false;
    },
    dialogSubmitForm() {
      let This = this;
      for (var i in this.addmultipleSelection) {
        this.dialogmultipleSelection.push(
          this.addmultipleSelection[i].company_claim_goods_id
        );
      }
      let addr = this.dialogmultipleSelection;
      this.$axios({
        method: "get",
        url: api.getGoodsListPublics,
        params: {
          company_claim_goods_id: addr.toString()
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$emit("callBack", res.data.data);
          let idArr = this.$parent.TableData.tableData.map(item => {
            return item.company_claim_goods_id;
          });
          res.data.data.forEach(item => {
            if (!idArr.includes(item.company_claim_goods_id)) {
              this.$parent.TableData.tableData.push(
                Object.assign({}, item, {
                  business_type: 1,
                  goods_price: item.goods_price,
                  tire_un: item.tire_un
                })
              );
            }
          });
          if (res.data.data[0].customer_id !== this.customerId) {
            this.$parent.params.customer_id = this.params.customer_id;
            this.$set(this.$parent, "customerList", [
              {
                customer_id: res.data.data[0].customer_id,
                customer_name: res.data.data[0].customer_name
              }
            ]);
            this.$parent.getCustomerInfo(3);
          }
          this.$parent.salesDocumentsVisible = false;
        }
      });
    }
  },
  mounted() {
    this.$set(this, "params", {
      company_id: this.companyId,
      customer_id: this.customerId,
      customer_name: this.customerName
    });
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "referenceClaimList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
    opthionList(this, this.params);
  }
};
</script>

