<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>售后管理</el-breadcrumb-item>
      <el-breadcrumb-item>公司三包</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">三包订单</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{threeorderAdd_Get.amend ? "修改" : "新增" }}三包订单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container hkfsUI" v-clock>
      <el-form :inline="true" :model="params" ref="TableDataRef">
        <el-form-item label="销售组织" :rules="rules.company_id" prop="company_id" size="mini">
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable :disabled="threeorderAdd_Get.amend ? true : false">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1?'optionStyle':''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="客户名称" :rules="rules.customer_id" prop="customer_id" size="mini">
          <el-select v-model="params.customer_id" filterable clearable placeholder="请输入" @change="getCustomerInfo" @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok" :disabled="threeorderAdd_Get.amend ? true : false">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="部门" size="mini">
          {{(function(){if(params.department_id == 0)params.department_id = null})()}}
          <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok" @change="departmentChange">
            <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务员" size="mini">
          {{(function(){if(params.business_id == 0)params.business_id = null})()}}
          <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok(1)">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="业务日期" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" type="date" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="出库类型" size="mini">
          <template>
            <span>三包出库</span>
          </template>
        </el-form-item>
        <!-- prop="delivery_demand_id" :rules="rules.delivery_demand_id" -->
        <el-form-item label="送货需求" size="mini">
          {{(function(){if(params.delivery_demand_id == 0)params.delivery_demand_id = null})()}}
          <el-select clearable v-model="params.delivery_demand_id" @focus="getDeliveryDemandListInvok">
            <el-option v-for="item in deliveryDemandList" :label="item.delivery_demand_name" :value="item.delivery_demand_id" :key="item.delivery_demand_id">
            </el-option>
          </el-select>
        </el-form-item>
        <!-- prop="hand_over_id" :rules="rules.hand_over_id" -->
        <el-form-item label="交货方式" size="mini">
          {{(function(){if(params.hand_over_id == 0)params.hand_over_id = null})()}}
          <el-select clearable v-model="params.hand_over_id" @focus="getHandoverListInvok">
            <el-option v-for="item in handoverList" :label="item.hand_over_name" :value="item.hand_over_id" :key="item.hand_over_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所属区域" size="mini" class="wauto" prop="cityList">
          <getAreaList v-if="defaultArea && defaultArea.length" @changeDefaultArea="changeDefaultArea" :defaultArea="defaultArea" />
        </el-form-item>
        <el-form-item label="收货人" size="mini" prop="consignee_name" :rules="rules.consignee_name">
          <el-input v-model="params.consignee_name" maxLength="7"></el-input>
        </el-form-item>
        <!-- prop="consignee_mobile" :rules="rules.phone" -->
        <el-form-item label="手机号码" size="mini">
          <el-input v-model="params.consignee_mobile"></el-input>
        </el-form-item>
        <el-form-item label="回款方式" size="mini">
          <el-select v-model="params.payment_method">
            <el-option label="客户自结" :value="1">客户自结</el-option>
            <el-option label="物流代收" :value="2">物流代收</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="详细地址" size="mini" prop="address" :rules="rules.address">
          <el-input v-model="params.address"></el-input>
        </el-form-item>
        <el-form-item label="意向物流" size="mini" prop="intentional_logistics_id">
          {{(function(){if(params.intentional_logistics_id == 0)params.intentional_logistics_id = null})()}}
          <el-select clearable v-model="params.intentional_logistics_id" filterable @focus="logisticsInvok" @change="logisticsChanges" :disabled="addDisabled" :filter-method="logisticsInvok">
            <el-option v-for="item in logisticsList" :label="item.logistics_name" :value="item.logistics_id" :key="item.logistics_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="摘要" size="mini">
          <el-input v-model="params.abstract"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini">引用单据</el-button>
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addLineF" class="addBtn" size="mini">增行</el-button>
      <el-button plain icon="el-icon-delete" @click="deleteF" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <xTable ref='xTable' :isEdit='isEdit' :tableColumn='tableColumn' :showFooter='true' :footerMethod="footerMethod" />
    </div>
    <br />
    <el-form :model="params" class="priceFoot" ref="TableDataRef2" :inline="true">
      <el-form-item label="本单使用返利：">
        <el-input v-model="params.rebate_price" type="input" class="element_input" @change="countSettlementPrice" suffix-icon="el-icon-edit-outline" :disabled="Number(params.rebate_balance) <=0 ||totalPrice<0"></el-input>
        <span class="yeban">余额:{{params.rebate_balance?params.rebate_balance:0|priceFormat}}</span>
      </el-form-item>
      <el-form-item label="本单使用授信：">
        <el-input v-model="params.credit_price" class="element_input" @change="countSettlementPrice" type="input" suffix-icon="el-icon-edit-outline" :disabled="Number(params.credit_balance) <=0||totalPrice<0"></el-input>
        <span class="yeban">余额:{{params.credit_balance?params.credit_balance:0|priceFormat}}</span>
      </el-form-item>
      <el-form-item label="本单使用预收：">
        <el-input v-model="params.deposits_price" class="element_input" suffix-icon="el-icon-edit-outline" type="input" @change="countSettlementPrice" :disabled="Number(params.deposits) <= 0||totalPrice<0"></el-input>
        <span class="yeban">余额:{{params.deposits?params.deposits:0|priceFormat}}</span>
      </el-form-item>
      <el-form-item label="客户余额：">
        <el-input v-model="params.balance" class="element_input" disabled></el-input>
        <el-button type="primary" @click="seeMoneyDetail">查看往来</el-button>
      </el-form-item>
      <el-form-item label="本单结算金额：">
        <span>{{params.settlement_price}}</span>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">取 消</el-button>
      <el-button type="primary" @click="submit('TableDataRef')">确 定</el-button>
    </div>
    <threeorderDalog ref="company" @callBack='orderSourceCallBack' v-if="salesDocumentsVisible" :companyId="params.company_id" :customerId="params.customer_id" :customerName="params.customer_name" />
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import threeorderDalog from "@/components/element/aftersale/share/threeorder_dalog.vue";
import getAreaList from "common/components/public/getAreaList";
import rebateCustom from "@/components/element/sales/share/rebateCustom"; //选择客户返利
import Stock from "./stock_dalog.vue";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
import {
  TableHead,
  rowModule,
  sumbitRowModule,
  paramsModule
} from "./js/threeorder_Add";
export default {
  name: "aftersaleThreeorderAdd",
  mixins: [TableHead],
  data() {
    return {
      defaultArea: [0], //默认的区域id
      salesDocumentsVisible: false, //货位列表弹框
      rebateTypeList: {},
      status: null,
      totalPrice: 0, //总价
      params: {},
      loading: false
    };
  },
  components: {
    getAreaList, //区域列表
    threeorderDalog, //引用公司鉴定单据
    rebateCustom, //选择客户返利
    Stock //引入弹框
  },
  methods: {
    ...mapActions(["threeorderIndexACT", "three_seeMoney"]),
    logisticsChanges(val) {
      if (val) {
        this.params.logistics_name = this.logisticsList.filter(
          obj => obj.logistics_id == val
        )[0].logistics_name;
      } else {
        this.params.logistics_name = "";
      }
    },
    paymentMethodChange() {
      this.$set(this.params, "settlement_customer_id", 0);
    },
    goBack() {
      // wz annotation cache
      this.threeorderIndexACT({
        params: this.threeorderIndex_Get ? this.threeorderIndex_Get.params : {},
        goPage: this.threeorderAdd_Get.goPage,
        pageSize: this.threeorderAdd_Get.pageSize
      });
      this.$router.push({
        path: "/aftersale/threeorder"
      });
    },
    seeMoneyDetail() {
      this.$router.push({ path: "/aftersale/threeorder/three_seeMoney" });
    },
    addF() {
      //引用单据
      if (!this.params.customer_id) {
        this.$message({
          message: "请先选择客户名称",
          type: "warning"
        });
      } else {
        this.salesDocumentsVisible = true;
      }
    },
    // 来源单号
    orderSourceCallBack(box) {
      let idArr = this.$refs.xTable
        ._proxy("getTableData")
        .fullData.concat([])
        .map(item => {
          return item.company_claim_goods_id;
        });
      let result = this.$refs.xTable._proxy("getTableData").fullData.concat([]);
      box.forEach(item => {
        if (!idArr.includes(item.company_claim_goods_id)) {
          result.push(
            Object.assign({}, item, {
              business_type: 1,
              goods_price: item.goods_price,
              tire_un: item.tire_un,
              goods_num: 0
            })
          );
        }
      });
      this.$refs.xTable._proxy("loadData", result);
    },
    getCustomerInfo(status) {
      //获取客户资料详情
      let params = {
        customer_id: this.params.customer_id,
        is_three_packs: 1
      };
      if (status) {
        params.company_id = this.customerList.filter(
          obj => obj.customer_id === status
        )[0].company_id;
      }
      if (!status) {
        this.params.consignee_name = ""; // 初始化收货人信息
        this.defaultArea = [0]; // 初始化所属区域
        this.params.address = ""; // 初始化详细地址
        this.params.consignee_mobile = ""; // 初始话手机号码
        return;
      }
      if (this.params.customer_id) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id == this.params.customer_id
        )[0].customer_name;
      }
      this.$axMethod
        .getCustomerInfoNEWqx(params)
        .then(({ data }) => {
          Object.assign(data.data, {
            employee_id: data.data.business_id,
            employee_name: data.data.business_name
          });
          opthionList(this, data.data);
          // bindIdsF(this, data.data, [
          //   ["employee_id", "employee_name", "employeeList", "options"]
          // ]);
          let arr = [
            "rebate_balance",
            "customer_mobile",
            "business_id",
            "deposits",
            "balance",
            "credit_balance"
          ].forEach(val => {
            if (val == "customer_mobile") {
              this.$set(
                this.params,
                "consignee_mobile",
                data.data["customer_mobile"]
              );
            } else {
              this.$set(this.params, val, data.data[val]);
            }
          });
          if (status != 3) {
            this.$refs.xTable._proxy("loadData", []);
          }
        })
        .then(res => {
          this.$axMethod.getDefaultAddress(params).then(({ data }) => {
            if (data.code == 200) {
              this.$set(this.params, "address", data.data.address);
              this.$set(
                this.params,
                "consignee_name",
                data.data.consignee_name
              );
              this.defaultArea = [
                data.data.province_id,
                data.data.city_id,
                data.data.area_id
              ];
              this.params.cityList = this.defaultArea.join(",");
              ["province_id", "city_id", "area_id"].forEach((val, index) => {
                this.params[val] = this.defaultArea[index];
              });
            }
          });
        });
    },
    countSettlementPrice(val = 0) {
      // 本单使用返利+本单使用授信+本单结算金额=订单的合计金额；
      if (val) {
        let valArry = ["rebate_price", "credit_price", "deposits_price"];
        valArry.forEach(value => {
          this.$set(
            this.params,
            value,
            !this.params[value] || this.params[value] < 0
              ? null
              : this.params[value]
          );
        });
      }
      let settlement_price =
        Number(this.totalPrice) -
        Number(this.params.rebate_price) -
        Number(this.params.credit_price) -
        Number(this.params.deposits_price);
      this.$set(this.params, "settlement_price", settlement_price.toFixed(2));
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    submit(formName) {
      /* 格式化参数 */
      if (!this.params.rebate_price) {
        this.params.rebate_price = 0;
      }
      this.countSettlementPrice();
      this.params.goods_json = [];
      var tableBox = this.$refs.xTable
        ._proxy("getTableData")
        .fullData.concat([]);
      if (tableBox.length === 0) {
        this.$message.warning("请先添加单据！");
        return false;
      }
      if (!tableBox[tableBox.length - 1].goods_name) {
        tableBox.pop();
      }
      if (
        Number(this.params.rebate_price) > Number(this.params.rebate_balance)
      ) {
        this.$message.warning("本单返利金额不得大于返利余额！");
        return false;
      }
      if (
        Number(this.params.credit_price) > Number(this.params.credit_balance) &&
        Number(this.params.credit_balance) >= 0
      ) {
        this.$message.warning("本单使用授信不得大于授信额度！");
        return false;
      }
      if (
        Number(this.params.deposits_price) > Number(this.params.deposits) &&
        Number(this.params.deposits) >= 0
      ) {
        this.$message.warning("本单使用预收不得大于客户预收余额！");
        return false;
      }
      let flag = true;
      this.$refs[formName].validate(valid => {
        if (valid) {
          //2 + 批次id及数量
          tableBox.forEach(item => {
            this.params.goods_json.push(
              this.$publicFution.filter(sumbitRowModule, item)
            );
          });
          let invalideArr = this.params.goods_json.filter((item, index) => {
            item.goods_price = Number(item.goods_price);
            item.new_goods_price = Number(item.new_goods_price);
            item.goods_num = Number(item.goods_num);
            let canSubmit =
              item.goods_id &&
              (item.new_goods_price != 0 || item.new_goods_price === 0) &&
              (item.goods_price != 0 || item.goods_price === 0) &&
              (item.goods_num != 0 || item.goods_num === 0);

            if (!canSubmit) {
              item.index = index + 1;
              return item;
            }
          });
          if (invalideArr.length) {
            this.$message.warning(
              invalideArr[0].goods_id && invalideArr[0].goods_name != undefined
                ? `请完善 ${invalideArr[0].goods_name} 商品信息`
                : `第 ${invalideArr[0].index} 条商品未填写完整`
            );
            flag = false;
          }

          if (!flag) return false;
          this.params.outstorage_type_id = 10001;
          this.params.order_price = this.totalPrice;
          this.params.goods_json = JSON.stringify(this.params.goods_json);
          Object.assign(this.params, {
            intentional_logistics: this.params.logistics_name
          });
          let api = !!this.threeorderAdd_Get.amend ? "editOrder" : "addOrder";
          this.$axMethod[api](this.params).then(({ data }) => {
            if (data.code == "200") {
              this.$message.success(data.msg);
              this.goBack();
            }
          });
        }
      });
    },
    companyChange(val) {
      if (val == 1) {
        // getDepartmentListCommon(this, this.params);
      } else {
        // getDepartmentListCommon(this, this.params, 1);
        this.$set(this.params, "customer_id", "");
        this.$set(this.TableData, "tableData", []);
        this.params.credit_balance = null; //授信余额
        this.params.deposits = null; //授信余额
        this.params.balance = null; //客户余额
        this.params.rebate_balance = null; //客户返利余额
      }
      this.$set(this.params, "delivery_demand_id", 0); //送货需求字段
      this.$set(this.params, "hand_over_id", 0); //交货方式字段
      this.$set(this.params, "department_id", ""); //初始化部门下拉选择项
      this.$set(this.params, "business_id", ""); //初始化业务员
      this.$set(this.params, "intentional_logistics_id", ""); //初始化物流意向
      this.$set(this.params, "settlement_customer_id", ""); //初始化回款方式

      this.params.consignee_name = ""; // 初始化收货人信息
      this.defaultArea = [0]; // 初始化所属区域
      this.params.address = ""; // 初始化详细地址
      this.params.consignee_mobile = ""; // 初始话手机号码
    },
    departmentChange(val) {
      // if (val) {
      //   this.params.employee_name = this.employeeList.filter(
      //     obj => obj.employee_id === val
      //   )[0].employee_name;
      // }
      // this.$set(this.params, "business_id", ""); //初始化业务员
    }
  },
  computed: {
    ...mapGetters(["threeorderIndex_Get", "threeorderAdd_Get"])
  },
  mounted() {
    let _this = this;
    if (this.threeorderAdd_Get.amend) {
      let customerId = "";
      let order_id = {
        order_id: this.threeorderAdd_Get.order_id
      };
      this.$axMethod.getOrderInfo(order_id).then(({ data }) => {
        this.params = data.data.order_info;
        this.$refs.xTable._proxy("loadData", data.data.goods_list);
        customerId = data.data.order_info.customer_id;
        this.$set(
          this.params,
          "payment_method",
          data.data.order_info.payment_method
        );
        Object.assign(data.data.order_info, {
          logistics_id: data.data.order_info.intentional_logistics_id,
          logistics_name: data.data.order_info.intentional_logistics,
          employee_id: data.data.order_info.business_id,
          employee_name: data.data.order_info.business_name
        });
        opthionList(this, data.data.order_info);

        // wz 回款方式 单独处理
        this.logisticsList.push({
          logistics_id: data.data.order_info.intentional_logistics_id,
          logistics_name: data.data.order_info.intentional_logistics
        });

        this.defaultArea = [
          data.data.order_info.province_id,
          data.data.order_info.city_id,
          data.data.order_info.area_id
        ];
        this.params.cityList = this.defaultArea.join(",");
        this.params.province_id = this.defaultArea[0];
        this.params.city_id = this.defaultArea[1];
        this.params.area_id = this.defaultArea[2];
        this.$axMethod
          .getCustomerInfoNEWqx({
            customer_id: customerId,
            company_id: data.data.order_info.company_id,
            is_three_packs: 1
          })
          .then(res2 => {
            if (res2.data.code == 200) {
              this.$set(this.params, "balance", res2.data.data.balance);
              let arr = [
                {
                  topName: "rebate_balance",
                  bottomName: "rebate_price"
                },
                {
                  topName: "credit_balance",
                  bottomName: "credit_price"
                },
                {
                  topName: "deposits",
                  bottomName: "deposits_price"
                }
              ];
              arr.forEach(({ topName, bottomName }) => {
                this.$set(
                  this.params,
                  topName,
                  (
                    Number(res2.data.data[topName]) +
                    Number(this.params[bottomName])
                  ).toFixed(2)
                );
              });
            }
          });
      });
    } else {
      this.params = Object.assign(
        {},
        this.$publicFution.deepCopy(paramsModule),
        {
          business_time: this.$publicFution.formatData(new Date()),
          company_id: Number(localStorage.company_id),
          payment_method: 1
        }
      );
      opthionList(this, localStorage);
    }
    this.$common.options("getRebateTypeListOption", this.rebateTypeList);
  }
};
</script>

<style lang="less" scoped>
.title {
  line-height: 50px;
  border-bottom: 1px solid #dddddd;
}
.goods_ul2 {
